.gpt3__possibility{
    display: flex;
    direction: row;
}
.gpt3__possibility-image{
    flex: 1;
    display: flex;
    justify-container: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.gpt3__possibility-image img{
    width: 100%; 
    height: 100%;
}
.gpt3__possibility-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    
}
.gpt3__possibility-container h4{

    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5ff;
}
.gpt3__possibility-container h4:last-child{
    color: #ff8a71;


}
.gpt3__possibility-container p{

    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;

}
.gpt3__possibility-container h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;

}


@media screen and (max-width:950px) {
    .gpt3__possibility{
        flex-direction: column;
    }

    .gpt3__possibility-image{
        margin: 1rem 0;
    }
    .gpt3__possibility-container{
        margin-top: 2rem;
    }
}